.sallingcard{
    width: 100%;
    position: relative;
    padding-bottom: 18px;
    color: #0F55A7;
    border-bottom: 1px solid rgb(213, 213, 213);

    img{
        width: 80px;
        height: 80px;
        margin-top: 10px;
        border-radius: 10px;
        margin-left: 13px;
    }
    h5{
        position: absolute;
        top:10px;
        left: 105px;
        font-size: 18px;
        color: #474747;
    }
    span{
        position: absolute;
        top: 33px;
        left: 105px;
        font-size: 14px;
    }
    p{
        position: absolute;
        bottom: 0px;
        left: 105px;
        font-weight: 400;
    }
    div{
        position: absolute;
        right: 15px;
        bottom:15px;
        font-size: 20px;
        font-weight: 500;
    }
}

@media (max-width:380px) {
    .sallingcard{
        img{
            margin-left: 30px;
        }
        h5, span, p{
            left: 120px;
        }
    }
}